<template>
  <div id="app">
    <img alt="Vue logo" src="./assets/logo.png">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <div>
      <el-button type="primary" @click="getStmtData">fetch data</el-button><br/>
      <p>{{stmtdata}}</p><br/>
      <router-link to="/word-add-in-for-note">goto word</router-link><br/>
      <router-link to="/excel-add-in-for-note">goto excel</router-link><br/>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  data() {
    return {
      stmtdata: {}
    }
  },
  methods: {
    getStmtData() {
      let _this = this
      axios({
        method: 'get',
        url: '/getstmtdata',
      })
        .then(function (response) {
          console.log(response.data)
          _this.stmtdata = response.data
        });
    },
    setFilePath() {
      let _this = this
      axios({
        method: 'post',
        url: '/setFile',
        data: {
          'filePath':_this.filePath,
        }
      })
        .then(function (response) {
          _this.filePath = response.data
          console.log(response)
        });
    }
  }
}
</script>

